body {
  font-family: 'Cantarell', sans-serif;
}

#canvas {
  border: 1px solid black;
}

#new-canvas {
  border: 1px solid black;
}

.info {
  display: block;
  width: 90vw;
  margin: 0 auto;
}

.drop-container {
  width: 100%;
  height: 80vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#test {
  width: 50vw;
  height: 40vw;
  border: 5px dashed #00f534;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-align-content: center;
          align-content: center;
}

#test hr {
  width: 100%;
  opacity: 0;
}

label {
  font-size: 1.5vw;
  text-decoration: underline;
  -webkit-text-decoration-color: #ff4cb4;
          text-decoration-color: #ff4cb4;
}

label:hover {
  cursor: pointer;
}

#customFile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

#test h1 {
  font-size: 3vw;
}

input[type='submit'] {
  margin-top: 5vw;
  background-color: white;
  border: 2px solid #ff4cb4;
  border-radius: 5px;
  width: 10vw;
  height: 5vw;
  font-size: 1.5em;
  font-weight: bold;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  width: 80%;
  height: 50%;
  border-radius: 10px;
  text-align: center;
}

.modal-content img {
  display: block;
  margin: 0 auto;
  width: 300px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.text-center {
  margin-top: 1em;
  margin-bottom: 1em;
}

#canvas {
  width: 60vw;
}

#glyphs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 1rem !important;
  width: 90vw;
  margin: 0 auto;
}

#glyphs span {
  display: none;
}

#input {
  border: 1.5px solid #dbdbdb;
  height: 2.5rem;
}

#input:focus {
  border: 1.5px solid #838383;
}

#slider {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background: #ff4cb4;
  height: 3px;
  border-radius: 10px;
  margin-left: 1rem;
  margin-right: 1rem;
}

#slider::-webkit-slider-thumb {
  -webkit-appearance: none;
          appearance: none;
  background: #00f534;
}

#slider::-moz-range-thumb {
  background: #00f534;
}

.display-font-top {
  min-width: 100px;
  height: 2.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.glyph-canvas {
  border: 1px solid #dadce0;
}

* {
  margin: 0;
  padding: 0;
}

header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.nav-right {
  margin-left: auto;
  display: -webkit-flex;
  display: flex;
}

.nav-left {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}

p {
  font-size: 20px;
  font-weight: bold;
  margin-right: 5vw;
  color: black;
  text-decoration: none;
}

p:hover {
  cursor: pointer;
}

/*context menu ###################################### */
.context-menu {
  display: none;
  position: absolute;
  z-index: 10;
  margin-left: 5vw;
  border: 3px solid #ff4cb4;
  border-radius: 5px;
  width: 272px;
}

.context-menu-item {
  list-style-type: none;
  margin-top: 1vh;
  margin-bottom: 0.7vh;
}

.context-menu-item p:hover,
.context-menu-item a:hover {
  color: #00f534;
}

.context-menu-item a {
  color: #000;
  text-decoration: none;
}

.context-menu--active {
  display: block;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.riddle-form {
  height: 80%;
  display: block !important;
}

.riddle-form input,
.riddle-form label {
  margin-right: 5vw;
}

.riddle-submit {
  border: 3px solid #00f534;
  background-color: transparent;
  margin-top: 6vh !important;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 30px;
  margin: 4px 2px;
  cursor: pointer;
  padding: 15px 32px;
  font-family: 'Cantarell', sans-serif;
  font-weight: bold;
  border-radius: 5px;
}

.riddle-submit:hover {
  background-color: #ff4cb4;
  color: #fff;
  border: none;
}

.review-content,
.feedback-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border-radius: 5px;
  width: 80%;
}

.review-content h1 {
  text-align: center;
}

.reviews {
  display: grid;
  justify-items: center;
}

.review {
  width: 65%;
  min-height: 100px;
  border: 2px solid #38444d;
  border-radius: 3px;
}

.review input {
  border: none;
  border-bottom: 1px solid #38444d;
}

#name {
  font-weight: bold;
  font-size: 16px;
  width: 150px;
}

.feedback-content h1 {
  text-align: center;
}

.top {
  display: -webkit-flex;
  display: flex;
}

.rating {
  margin-left: auto;
}

.star:hover {
  color: orange;
}

.logo {
  font-size: 2.5rem;
}

