* {
  margin: 0;
  padding: 0;
}

header {
  display: flex;
  align-items: center;
}

.nav-right {
  margin-left: auto;
  display: flex;
}

.nav-left {
  display: flex;
  align-items: center;
  cursor: pointer;
}

p {
  font-size: 20px;
  font-weight: bold;
  margin-right: 5vw;
  color: black;
  text-decoration: none;
}

p:hover {
  cursor: pointer;
}

/*context menu ###################################### */
.context-menu {
  display: none;
  position: absolute;
  z-index: 10;
  margin-left: 5vw;
  border: 3px solid #ff4cb4;
  border-radius: 5px;
  width: 272px;
}

.context-menu-item {
  list-style-type: none;
  margin-top: 1vh;
  margin-bottom: 0.7vh;
}

.context-menu-item p:hover,
.context-menu-item a:hover {
  color: #00f534;
}

.context-menu-item a {
  color: #000;
  text-decoration: none;
}

.context-menu--active {
  display: block;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.riddle-form {
  height: 80%;
  display: block !important;
}

.riddle-form input,
.riddle-form label {
  margin-right: 5vw;
}

.riddle-submit {
  border: 3px solid #00f534;
  background-color: transparent;
  margin-top: 6vh !important;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 30px;
  margin: 4px 2px;
  cursor: pointer;
  padding: 15px 32px;
  font-family: 'Cantarell', sans-serif;
  font-weight: bold;
  border-radius: 5px;
}

.riddle-submit:hover {
  background-color: #ff4cb4;
  color: #fff;
  border: none;
}

.review-content,
.feedback-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border-radius: 5px;
  width: 80%;
}

.review-content h1 {
  text-align: center;
}

.reviews {
  display: grid;
  justify-items: center;
}

.review {
  width: 65%;
  min-height: 100px;
  border: 2px solid #38444d;
  border-radius: 3px;
}

.review input {
  border: none;
  border-bottom: 1px solid #38444d;
}

#name {
  font-weight: bold;
  font-size: 16px;
  width: 150px;
}

.feedback-content h1 {
  text-align: center;
}

.top {
  display: flex;
}

.rating {
  margin-left: auto;
}

.star:hover {
  color: orange;
}
